import React from 'react';
import s from './Header.module.css';
import ContantContainerMain from '../../utils/ContantContainerMain';
import logo from '../../assets/img/logos/full-logo.svg';
import icon from '../../assets/icons/version_visually_impaired.svg';

const Header = (props) => {
   return (
      <div>
         <div className={s.bgBlock}>
            <ContantContainerMain>
               <div className={s.logoRow}>
                  <div className={s.logoBlock}>
                     <img src={logo} alt="" />
                     <div className={s.textMain}>
                        <div className={s.titleInfo}> -</div>
                        <div className={s.titleAdministration}>   </div>
                        <div className={s.titleInfo}> </div>
                     </div>
                  </div>
                  <div className={s.settingsContainer}>
                     <div className={s.versionVI}><img src={icon} alt="" />  </div>
                     <button className={s.hotLine}> </button>
                  </div>
               </div>
            </ContantContainerMain>
         </div>
         <div className={s.mainMenuContainer}>
            <ContantContainerMain>
               <div className={s.mainMenu}>
                  <div className={s.itemsContainer}>
                     <div className={`${s.item} ${s.itemArrow}`}>
                        
                        <ul>
                           <li></li>
                           <li></li>
                           <li></li>
                        </ul>
                        </div>
                     <div className={`${s.item} ${s.itemArrow}`}>
                        
                        <ul>
                           <li></li>
                           <li>   </li>
                           <li>   </li>
                           <li>-</li>
                           <li> </li>
                        </ul>
                        </div>
                     <div className={`${s.item} ${s.itemArrow}`} >
                        
                        <ul>
                           <li>  </li>
                           <li> </li>
                        </ul>
                        </div>
                     <div className={s.item}></div>
                     <div className={s.item}></div>
                     <div className={s.item}> </div>
                     <div className={`${s.item} ${s.itemArrow}`}>
                         
                        <ul>
                           <li>  </li>
                           <li> </li>
                        </ul>
                         </div>
                     <div className={s.item}></div>
                  </div>

                  <div className={s.searchBlock}>
                     
                     {/* <input type="text" placeholder='' className={s.search} /> */}
                  </div>
               </div>
            </ContantContainerMain>
         </div>
      </div >


   )
}
export default Header;